// Import the functions you need from the SDKs you need
import { initializeApp } from "firebase/app";
import { getAnalytics } from "@firebase/analytics";
// TODO: Add SDKs for Firebase products that you want to use
// https://firebase.google.com/docs/web/setup#available-libraries
import React, { useState, useEffect } from "react";

import {
	getDatabase,
	ref,
	set,
	push,
	onValue,
	off,
	remove
} from "firebase/database";

// Your web app's Firebase configuration
// For Firebase JS SDK v7.20.0 and later, measurementId is optional
const firebaseConfig = {
	apiKey: "AIzaSyBBxZtC7mLO7C3AKLM9L22FBTN6H0m1vk0",
	authDomain: "estudos-de-react.firebaseapp.com",
	projectId: "estudos-de-react",
	storageBucket: "estudos-de-react.appspot.com",
	messagingSenderId: "269154094895",
	appId: "1:269154094895:web:def8341693a0856ff51bda",
	measurementId: "G-W7FFEB2SZL",
	databaseURL: "https://estudos-de-react-default-rtdb.firebaseio.com"
};

// Initialize Firebase
const app = initializeApp(firebaseConfig);
const analytics = getAnalytics(app);

const database = getDatabase(app);

export default function Card() {
	const [name, setName] = useState("");
	const [message, setMessage] = useState("");

	const [nameList, setNameList] = useState([]);
	const [showList, setShowList] = useState(true);

	useEffect(() => {
		const starCountRef = ref(database, "/names");
		onValue(starCountRef, (snapshot) => {
			setNameList(
				Object.entries(snapshot.val()).map(([id, value]) => ({ id, ...value }))
			);
		});

		return () => off(starCountRef);
	}, []);

	function handleShowList(e) {
		e.preventDefault();
		setShowList(!showList);
	}

	function handleChange(e) {
		setName(e.target.value);
	}

	function handleSubmit(e) {
		e.preventDefault();

		push(ref(database, "names/"), {
			name: name
		});
		setName("");
		setMessage("Name saved successfully!");
	}

	function handleDelete(id) {
		const aux = ref(database, "/names/" + id);
		remove(aux);
	}

	return (
		<div>
			<form onSubmit={handleSubmit}>
				<input
					type="text"
					placeholder="Enter name"
					value={name}
					onChange={handleChange}
				/>
				<button type="submit">Save</button>
			</form>
			{message && <p>{message}</p>}
			<button onClick={handleShowList}>
				{showList ? "Hide" : "Show"} List
			</button>
			{showList && (
				<ul>
					{nameList.map(({ id, name }) => (
						<li key={id}>
							{name + "     "}
							<button onClick={() => handleDelete(id)}>X</button>
						</li>
					))}
				</ul>
			)}
		</div>
	);
}
